.wrkshopsBackgroundTop {
  background-color: rgb(0, 20, 0);
  height: auto;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.outer_circle {
  margin: auto;
  margin-top: 5rem;
  margin-bottom: 10rem;
  position: relative;
  width: 65vh;
  height: 65vh;
  z-index: 1;
  background: linear-gradient(
    to bottom,
    rgba(44, 232, 26, 0.758) 0%,
    rgb(0, 45, 0) 50%,
    rgb(0, 20, 0) 80%
  );
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.inner_circle {
  position: absolute;
  background-color: rgb(0, 20, 0);
  border-radius: 50%;
  width: 55vh;
  height: 55vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.workshpImg {
  z-index: 1;
  position: absolute;
  top: -1rem;
  width: 70%;
  height: 70%;
  /* left: 1.5rem; */
  /* opacity: 0.5; */
  margin: auto;
  animation: animate 2s linear infinite;
}

@keyframes animate {
  50% {
    transition: 1s ease;
    transform: translateY(-20px);
  }

  100% {
    transition: 1s ease;
    transform: translateY(0px);
  }
}

.explore_wrkshop {
  z-index: 1;
  color: white;
  position: relative;
  text-align: center;
  top: 22vh;
}

.explore_wrkshop h2 {
  font-family: "monumentExtended";
  color: white;
  font-size: 6vh; /*40px */
  letter-spacing: 2px;
  position: relative;
  margin-bottom: 0;
  /* top: 50px; */
  /* top: 10vh; */
  /* margin-top: 27vh; */
  /* padding: 2px 0 0 13px; */
}

.explore_wrkshop p {
  margin-top: 10px;
  text-align: justify;
}

/* Name of the Workshop */

.wrkshopsBackgroundBottoms {
  background: rgb(5, 79, 17);
  background: linear-gradient(
    135deg,
    rgba(5, 79, 17, 0.99) 9%,
    rgba(0, 0, 0, 1) 50%,
    rgba(5, 62, 15, 1) 93%
  );
  height: auto;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  padding-bottom: 10vh;
}

.workshopBox {
  margin: 2rem auto 7rem auto;
  height: auto;
  width: 80vw;
  border: 1px solid rgb(35, 185, 35);
  border-radius: 10px;
  background-color: rgb(0, 35, 0);
  /* background-color: rgb(53, 67, 48); */
  /* opacity: 0.5; */
  padding: 40px;
  position: relative;
  /* margin: 20px; */
}

.LeftOfBox {
  width: 45%;
  float: left;
  color: white;
  /* display: flex;
    flex-direction: column; */
  /* justify-content: left; */
}

.evntDesc {
  display: flex;
  /* justify-content: center; 
    align-items: flex-start; */
  flex-direction: column;
  margin-bottom: 10px;
}

.LeftOfBox h2 {
  margin-bottom: 10px;
}

.RightOfBox {
  width: 40%;
  float: right;
  bottom: 0px;
  display: flex;
  /* height: 43vh; */
  color: white;
  /* position: absolute; */
  /* display: flex;
    justify-content: left; */
}

.objOfBox {
  /* position: absolute; */
  bottom: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  /* flex-direction: row; */
}

.boxImg {
  height: 40%;
  width: 40%;
  border-radius: 50%;
}

#registerNow {
  padding: 8px 20px;
  background: linear-gradient(to right, rgb(47, 226, 47), rgb(8, 81, 8));
  border-radius: 10px;
  color: white;
  margin: 15px 20px 7px 0;
  float: left;
  text-align: left;
  cursor: pointer;
  border: 1px solid rgb(47, 226, 47);
}

#viewSchedule {
  padding: 8px 23px;
  background: linear-gradient(to right, rgb(90, 86, 86), rgb(30, 27, 27));
  border-radius: 10px;
  color: white;
  margin: 15px 25px 7px 0;
  float: left;
  cursor: pointer;
  border: 1px solid rgb(90, 86, 86);
}

@media screen and (max-width: 1055px) {
  .boxImg {
    width: 70%;
  }
  .explore_wrkshop {
    margin-top: 15%;
  }
  .outer_circle {
    margin-bottom: 13rem;
  }
}

@media screen and (max-width: 777px) {
  .outer_circle {
    width: 60vw;
    height: 60vw;
  }
  .inner_circle {
    width: 50vw;
    height: 50vw;
  }
  /* .explore_wrkshop{
        top: 8.5vh;
    } */
  .explore_wrkshop h2 {
    font-size: 6vw;
  }
  .LeftOfBox {
    width: 50%;
  }
  .RightOfBox {
    width: 45%;
  }
  .objOfBox {
    font-size: 2vw;
  }
}

@media screen and (max-width: 650px) {
  .outer_circle {
    width: 65vw;
    height: 65vw;
    margin-bottom: 15rem;
  }
  .inner_circle {
    width: 55vw;
    height: 55vw;
  }
  .workshpImg {
    width: 73%;
    height: 73%;
  }
  .explore_wrkshop {
    margin-top: 25%;
  }
  .explore_wrkshop h2 {
    font-size: 7vw;
    /* top: 6vh; */
  }
  .explore_wrkshop p {
    font-size: 1.3em;
  }
  .workshopBox {
    margin: 2rem 1rem 3rem 1rem;
    padding: 25px 25px 27vw 25px;
    width: 85%;
    height: auto;
  }
  .RightOfBox {
    width: 100%;
    float: none;
  }
  .LeftOfBox {
    width: 100%;
    float: none;
  }
  .objOfBox {
    font-size: 2.8vw;
    bottom: auto;
    text-align: center;
  }
  .boxImg {
    height: 30%;
    width: 30%;
  }
}

@media screen and (max-width: 550px) {
  .explore_wrkshop p {
    font-size: 1.2em;
  }
  .objOfBox {
    font-size: 3vw !important;
  }
  .outer_circle {
    margin-bottom: 17rem;
  }
}

@media screen and (max-width: 456px) {
  #viewSchedule {
    margin-top: 8px;
  }
}

@media screen and (max-width: 386px) {
  /* .explore_wrkshop {
    top: 6.6vh;
  } */
  .explore_wrkshop h2 {
    font-size: 7vw;
    /* top: 3vh; */
  }
  #registerNow {
    margin: 7px 25px 0 0;
  }
  .outer_circle {
    margin-bottom: 20rem;
  }
}
